.container-m{
   position: fixed;
   width: 40%;
   padding: 24px 18px;
   background-color: white;
   border-radius: 25px;
   top: 50%;
   left: 50%;
   transform: translate(-50%, -50%);
   box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
}

.warning{
   border-style: solid;
   border-color: #1a032c;
}

.action-m{
   background-color: #4630ab;
   border-color: #1a032c;
}

.actions-container-m{
   display: flex;
   gap: 8px;
   margin-top: 8px;
}